let data = {
	selectedProgram : 0.075,
	cost            : 5000,
	minPrice        : 5000,
	maxPrice        : 1000000,
	minYear         : 12,
	maxYear         : 48,
	time            : 12,
	programs        : {
		base        : 0.075,
		pens        : 0.09,
		funded      : 0.065,
		urgent      : 0.06,
		comfortable : 0.065,
		vip         : 0.13,
		mbox        : 0.09,
		premium     : 0.12,
	},
};

let results = {
	rate : data.selectedProgram,
};

function getData() {
	return { ...data };
}

function getResults() {
	return { ...results };
}

function setData(newData) {
	// console.log('New data', newData);

	if (newData.onUpdate === 'radioProgram') {
		if (newData.id === 'base-value') {
			data.cost                                      = 5000;
			data.time                                      = 12;
			data.minPrice                                  = 5000;
			data.maxPrice                                  = 1000000;
			data.minYear                                   = 12;
			data.maxYear                                   = 48;
			document.querySelector('#price-min').innerHTML = `${data.cost} руб`;
			document.querySelector('#price-max').innerHTML = `${data.maxPrice} руб`;
			document.querySelector('#date-min').innerHTML  = `${data.minYear} месяцев`;
			document.querySelector('#date-max').innerHTML  = `${data.maxYear} месяцев`;
		}

		if (newData.id === 'pens-value') {
			data.cost                                      = 1000;
			data.time                                      = 12;
			data.minYear                                   = 12;
			data.maxYear                                   = 48;
			data.minPrice                                  = 1000;
			data.maxPrice                                  = 1000000;
			document.querySelector('#price-min').innerHTML = `${data.cost} руб`;
			document.querySelector('#price-max').innerHTML = `${data.maxPrice} руб`;
			document.querySelector('#date-min').innerHTML  = `${data.minYear} месяцев`;
			document.querySelector('#date-max').innerHTML  = `${data.maxYear} месяцев`;
		}

		if (newData.id === 'funded-value') {
			data.cost                                      = 5000;
			data.minYear                                   = 12;
			data.maxYear                                   = 24;
			data.time                                      = 12;
			data.minPrice                                  = 5000;
			data.maxPrice                                  = 1000000;
			document.querySelector('#price-min').innerHTML = `${data.cost} руб`;
			document.querySelector('#price-max').innerHTML = `${data.maxPrice} руб`;
			document.querySelector('#date-min').innerHTML  = `${data.minYear} месяцев`;
			document.querySelector('#date-max').innerHTML  = `${data.maxYear} месяцев`;
		}

		if (newData.id === 'urgent-value') {
			data.cost                                      = 5000;
			data.minPrice                                  = 5000;
			data.maxPrice                                  = 1000000;
			data.time                                      = 6;
			data.minYear                                   = 6;
			data.maxYear                                   = 9;
			document.querySelector('#price-min').innerHTML = `${data.cost} руб`;
			document.querySelector('#price-max').innerHTML = `${data.maxPrice} руб`;
			document.querySelector('#date-min').innerHTML  = `${data.minYear} месяцев`;
			document.querySelector('#date-max').innerHTML  = `${data.maxYear} месяцев`;
		}

		if (newData.id === 'comfortable-value') {
			data.cost                                      = 5000;
			data.minPrice                                  = 5000;
			data.maxPrice                                  = 1000000;
			data.time                                      = 1;
			data.minYear                                   = 1;
			data.maxYear                                   = 48;
			document.querySelector('#price-min').innerHTML = `${data.cost} руб`;
			document.querySelector('#price-max').innerHTML = `${data.maxPrice} руб`;
			document.querySelector('#date-min').innerHTML  = `${data.minYear} месяца`;
			document.querySelector('#date-max').innerHTML  = `${data.maxYear} месяцев`;
		}

		if (newData.id === 'vip-value') {
			data.cost                                      = 1000;
			data.time                                      = 18;
			data.minYear                                   = 18;
			data.maxYear                                   = 48;
			data.minPrice                                  = 1000;
			data.maxPrice                                  = 2000000;
			document.querySelector('#price-min').innerHTML = `${data.cost} руб`;
			document.querySelector('#price-max').innerHTML = `${data.maxPrice} руб`;
			document.querySelector('#date-min').innerHTML  = `${data.minYear} месяцев`;
			document.querySelector('#date-max').innerHTML  = `${data.maxYear} месяцев`;
		}

		if (newData.id === 'mbox-value') {
			data.cost                                      = 5000;
			data.time                                      = 12;
			data.minYear                                   = 12;
			data.maxYear                                   = 48;
			data.minPrice                                  = 5000;
			data.maxPrice                                  = 2000000;
			document.querySelector('#price-min').innerHTML = `${data.cost} руб`;
			document.querySelector('#price-max').innerHTML = `${data.maxPrice} руб`;
			document.querySelector('#date-min').innerHTML  = `${data.minYear} месяцв`;
			document.querySelector('#date-max').innerHTML  = `${data.maxYear} месяцев`;
		}

		if (newData.id === 'premium-value') {
			data.cost                                      = 100000;
			data.time                                      = 18;
			data.minYear                                   = 18;
			data.maxYear                                   = 36;
			data.minPrice                                  = 100000;
			data.maxPrice                                  = 2000000;
			document.querySelector('#price-min').innerHTML = `${data.cost} руб`;
			document.querySelector('#price-max').innerHTML = `${data.maxPrice} руб`;
			document.querySelector('#date-min').innerHTML  = `${data.minYear} месяцв`;
			document.querySelector('#date-max').innerHTML  = `${data.maxYear} месяцев`;
		}
	}

	data = {
		...data,
		...newData,
	};

	// Рассчет ипотеки
	const months = data.time;  // получаем месяцы
	// console.log(months);
	const totalAmount = data.cost; // входящие деньги
	const monthRate   = data.selectedProgram / 12; // получаем процентную ставку
	const generalRate = (1 + monthRate) ** months;
	const monthIncome = totalAmount * generalRate;

	results = {
		rate : data.selectedProgram,
		totalAmount,
		monthIncome,
	};

	// console.log('Updated data', data);
	// console.log('New resulst', results);
}

export { getData, setData, getResults };
