import updateModel from '../utils/updateModel.js';

function init(getData) {
	const radioBtns = document.querySelectorAll('input[name="program"]');
	const {
			  base,
			  pens,
			  funded,
			  urgent,
			  comfortable,
			  vip,
			  mbox,
			  premium,
		  }         = getData().programs;

	// Set program rates in radio buttons
	document.querySelector('#base-value').value        = base;
	document.querySelector('#pens-value').value        = pens;
	document.querySelector('#funded-value').value      = funded;
	document.querySelector('#urgent-value').value      = urgent;
	document.querySelector('#comfortable-value').value = comfortable;
	document.querySelector('#vip-value').value         = vip;
	document.querySelector('#mbox-value').value        = mbox;
	document.querySelector('#premium-value').value     = premium;

	radioBtns.forEach((radioBtn) => {
		radioBtn.addEventListener('change', function () {
			updateModel(this, {
				onUpdate        : 'radioProgram',
				selectedProgram : parseFloat(this.value),
				id              : this.id,
			});
		});
	});

	const optionMenu = document.querySelector('.select__wrapper');
	const selectBtn  = optionMenu.querySelector('.select__head');
	const options    = optionMenu.querySelectorAll('.select__item');
	const selected   = optionMenu.querySelector('.select__selected');

	if (selectBtn) {
		selectBtn.addEventListener('click', () => optionMenu.classList.toggle('active'));
		options.forEach((option) => {
			option.addEventListener('click', () => {
				const selectedOption = document.querySelector('.radio-hidden:checked + label').innerHTML;
				selected.innerHTML   = selectedOption;
				optionMenu.classList.remove('active');
			});
		});

		document.addEventListener('click', (e) => {
			if (!optionMenu.contains(e.target) && !selectBtn.contains(e.target)) {
				optionMenu.classList.remove('active');
			}
		});
	}
}

export default init;
