import * as Model        from './calculator/model.js';
import updateResultsView from './calculator/view/updateResultsView.js';
import programs          from './calculator/view/radioPrograms.js';

import costInput from './calculator/view/costInput.js';
import costRange from './calculator/view/costRange.js';

import timeInput from './calculator/view/timeInput.js';
import timeRange from './calculator/view/timeRange.js';

window.onload = function () {
	const { getData } = Model;

	// Init programs
	programs(getData);

	// Init Cost input
	const cleaveCost = costInput(getData);
	const sliderCost = costRange(getData);

	// Time
	const cleaveTime = timeInput(getData);
	const sliderTime = timeRange(getData);

	Model.setData({});
	const results = Model.getResults();
	updateResultsView(results);

	document.addEventListener('updateForm', (e) => {
		Model.setData(e.detail);

		const data    = Model.getData();
		const results = Model.getResults();

		// Update all form view based on model
		updateFormAndSliders(data);

		// Update results block
		updateResultsView(results);
	});

	function updateFormAndSliders(data) {
		// Update radio btns
		if (data.onUpdate === 'radioProgram') {
			sliderCost.noUiSlider.updateOptions({
				range : {
					min : data.minPrice,
					max : data.maxPrice,
				},
			});

			sliderTime.noUiSlider.updateOptions({
				range : {
					min : data.minYear,
					max : data.maxYear,
				},
			});
		}

		// costInput
		if (data.onUpdate !== 'inputCost') {
			cleaveCost.setRawValue(data.cost);
		}

		// costSlider
		if (data.onUpdate !== 'costSlider') {
			sliderCost.noUiSlider.set(data.cost);
		}

		// timeInput
		if (data.onUpdate !== 'inputTime') {
			cleaveTime.setRawValue(data.time);
		}

		// timeSlider
		if (data.onUpdate !== 'timeSlider') {
			sliderTime.noUiSlider.set(data.time);
		}
	}
};
